html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::after,
*::before {
  -webkit-tap-highlight-color: transparent;
}

body {
  overflow-x: hidden;
  margin: 0;
  position: relative;
}

ul,
li,
h1,
h2,
h3,
h4,
p {
  list-style: none;
  margin: 0;
  padding: 0;
}

h2 {
  color: #191919;
  font-family: 'Poiret One', cursive;
  font-size: 20px;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 1.4px;
  text-transform: uppercase;
  text-align: center;
}

@media(min-width:1440px) {
  h2 {
    font-size: 36px;
    letter-spacing: 1.8px;
  }
}

h3 {
  color: #FBFBFB;
  font-family: 'Poiret One', cursive;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  letter-spacing: 1.6px;
  text-transform: uppercase;
}

@media(min-width:1440px) {
  h3 {
    font-size: 24px;
    letter-spacing: 1.2px;
  }
}

a {
  display: inline-block;
  text-decoration: none;
}

p {
  margin: 0;
  color: #191919;
  text-align: center;
  font-family: 'Manrope', sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
  letter-spacing: 0.32px;
}

@media(min-width:1440px) {
  p {
    font-size: 18px;
  }
}

//header start

.header {
  &-container {
    background-color: #2E563C;
    padding: 0 16px;

    &.menu-page {
      background-color: #fff;
    }
  }
}

@media(min-width:640px) {
  .header {
    &-container {
      padding: 0 36px;
    }
  }
}

@media(min-width:1024px) {
  .header {
    &-container {
      padding: 0 125px;
    }
  }
}

@media(min-width:1920px) {
  .header {
    &-container {
      padding: 0 375px;
    }
  }
}

.sticky {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}

.header-inner {
  padding: 16px 0;
  display: flex;
  justify-content: space-between;
}

.menu-desktop {
  display: none;
}

@media(min-width:1024px) {
  .menu-desktop {
    display: flex;
    gap: 24px;
    align-items: center;

    a {
      color: #1E1E1E;
      text-align: center;
      font-family: Manrope;
      font-size: 16px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
      letter-spacing: 1.28px;
    }

    &.menu-page {
      a:first-child {
        border-color: #1E1E1E;
      }

      a:hover {
        animation: bounce2 2s ease infinite;

        @keyframes bounce2 {

          0%,
          20%,
          50%,
          80%,
          100% {
            transform: translateY(0);
          }

          40% {
            transform: translateY(-30px);
          }

          60% {
            transform: translateY(-15px);
          }
        }
      }
    }

    a:first-child {
      padding: 12px 28px;
      border: #FBFBFB solid 1px;
    }
  }
}

@media(min-width:1440px) {
  .menu-desktop {
    gap: 60px;

    a {
      font-size: 18px;
    }
  }
}

.menu-mobile {
  display: flex;
  background-color: #2E563C;
  align-items: center;

  &.menu-page {
    background-color: #fff;
  }
}

select {
  &:focus {
    outline: none;
    border: none;
  }

  &.menu-page {
    background-color: #fff;
    color: #1E1E1E;
    border: none;
    min-height: 25px;
  }
}

@media(min-width:1440px) {
  select {
    &.menu-page {
      margin-top: 10px;
    }
  }
}

.menu {
  padding-left: 24px;
}

@media(min-width:1024px) {
  .menu {
    display: none;
  }
}

.like {
  padding-left: 10px;
}

.menu-popup {
  position: fixed;
  top: 0;
  left: 0;
  display: inline-block;
  transition: inherit;
  text-align: center;
  z-index: 200;
  width: 100%;
  overflow: hidden;
  visibility: visible;
  opacity: 1;

  .menu-popup_inner {
    padding: 16px;
    z-index: 999;
    border-radius: 0px 0px 18px 18px;
    border-bottom: 2px solid #616161;
    background: #FBFBFB;

    .header-menu {
      display: flex;
      justify-content: space-between;

      .right-box {
        display: flex;
        background-color: #fbfbfb;

        select {
          background-color: #fbfbfb;
        }

        .menu {
          padding-top: 30px;

          svg {
            display: block;
            margin: auto;
          }
        }
      }
    }

    .navigation {
      display: flex;
      flex-direction: column;
      gap: 24px;
      align-items: flex-end;

      a {
        color: #A9A9A9;
        font-family: Manrope;
        font-size: 16px;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        letter-spacing: 1.28px;
        text-transform: uppercase;
      }

      a:hover {
        color: #172B1E;
        font-weight: 700;
      }
    }
  }
}

@media(min-width:640px) {
  .menu-popup {
    .menu-popup_inner {
      padding: 16px 36px;
    }
  }
}

// navigation
.menu-navigation {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  overflow-x: auto;
  white-space: nowrap;
  z-index: 1;

  h2 {
    max-width: 320px;
    text-align: center;
    margin-top: 110px;
    margin-bottom: 40px;
  }
}

@media(min-width:375px) {
  .menu-navigation {
    h2 {
      max-width: 375px;
      margin-bottom: 32px;
    }
  }
}

@media(min-width:425px) {
  .menu-navigation {
    h2 {
      max-width: 425px;
      margin-bottom: 32px;
    }
  }
}

@media(min-width:640px) {
  .menu-navigation {
    h2 {
      max-width: 100%;
      margin-bottom: 32px;
    }
  }
}

@media(min-width:1440px) {
  .menu-navigation {
    h2 {
      margin-bottom: 40px;
    }
  }
}

.menu-scroll_inner {
  overflow-x: auto;
}

@media(min-width:1440px) {
  .menu-scroll_inner {
    min-width: none;
    width: 1160px;
    margin: 0 auto;
  }
}

.card-wrapper {
  display: flex;
  justify-content: flex-start;
  box-sizing: content-box;
  overflow-x: auto;
  min-width: none;
  min-height: 100%;
  gap: 0;

}

@media(min-width:1024px) {
  .card-wrapper {
    justify-content: flex-start;
  }
}

@media(min-width:1440px) {
  .card-wrapper {
    min-width: none;
    width: 1160px;
    margin: 0 auto;
  }
}

.card-wrapper::-webkit-scrollbar {
  display: none;
}

li {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-right: 32px;
  border-bottom: 2px solid #CDCDCD;
  border-radius: 150px 150px 0px 0px;
  background: rgba(251, 251, 251, 0.10);
  backdrop-filter: blur(10px);
  box-sizing: border-box;

  &:hover {
    border-bottom: 2px solid #FF0000;
    transition: 0.5s;
  }

  h3 {
    padding-bottom: 7px;
    color: #1E1E1E;
  }

  &:first-child {
    margin-left: 30px;
  }

  &:last-child {
    padding-right: 0;
    margin-right: 40px;
  }
}

.active {
  border-bottom: 2px solid #FF0000;
  transition: 0.5s;
}

@media(min-width:1024px) {
  li {
    &:first-child {
      margin-left: 80px;
    }

    &:last-child {
      margin-right: 50px;
    }
  }
}

@media(min-width:1440px) {
  li {
    &:first-child {
      margin-left: 0px;
    }

    &:last-child {
      margin-right: 0px;
    }
  }
}

@media(min-width:1920px) {
  li {
    &.menu-page {
      h3 {
        padding: 0;
      }
    }

    &.menu-page:first-child {
      margin-left: 375px;
    }
  }
}



// menu-deliery start

.menu-delivery {
  background-image: url(../../public/img/fon-menu.svg);
  background-position: -150px 0px;
  padding-top: 250px;
  z-index: 1;

  .container {
    width: 260px;
    display: block;
    margin: 0 auto;
  }
}

@media(min-width:640px) {
  .menu-delivery {
    .container {
      width: 568px;
    }
  }
}

@media(min-width:1024px) {
  .menu-delivery {
    .container {
      width: 865px;
    }
  }
}

@media(min-width:1440px) {
  .menu-delivery {
    padding-top: 300px;

    .container {
      width: 1280px;
    }
  }
}

.menu-delivery_inner {
  padding-bottom: 40px;

  h3 {
    color: #1E1E1E;
    padding-bottom: 24px;
  }
}

.ancor {
  padding-top: 240px;
  margin-top: -240px;
  -webkit-background-clip: content-box;
  background-clip: content-box;
}

@media(min-width:1440px) {
  .ancor {
    padding-top: 265px;
    margin-top: -265px;
    -webkit-background-clip: content-box;
    background-clip: content-box;
  }
}

.product-cards {
  display: flex;
  justify-content: center;
  gap: 24px;
  flex-wrap: wrap;
  padding: 0 10px;
  padding-bottom: 40px;
}

@media(min-width:640px) {
  .product-cards {
    padding-bottom: 40px;
    justify-content: flex-start;
  }
}

@media(min-width:1024px) {
  .product-cards {
    padding: 0;
    padding-bottom: 40px;
  }
}

.product-card {
  outline: none;
  width: 260px;
  border-radius: 8px;
  background: #FBFBFB;
  box-shadow: 0px 4px 8px 0px rgba(30, 30, 30, 0.25), 0px -2px 8px 0px rgba(30, 30, 30, 0.25);

  img {
    height: 223px;
    display: block;
    margin: 0 auto;
    padding: 0 20px;
    cursor: pointer;
  }

  .description {
    display: flex;
    justify-content: space-between;
    background: #2E563C;
    padding: 24px;

    h3 {
      color: #FBFBFB;
      min-height: 85px;
      padding: 0;
    }

    .like {
      cursor: pointer;

      &.red {

        #Vector,
        #Vector_2 {
          fill: #AE1F13
        }

        #Vector_3 {
          fill: #E73628;
        }

        rect {
          fill: #E73628;
        }
      }
    }
  }

  .price {
    display: flex;
    justify-content: space-between;
    padding: 16px;
  }
}

@media(min-width:1024px) {
  .product-card {
    .description {
      .like {
        svg:hover {

          #Vector,
          #Vector_2 {
            fill: #BD6D00;
          }

          #Vector_3 {
            fill: #F79E01;
          }

          rect {
            fill: #F79E01;
          }
        }

        svg:active {

          #Vector,
          #Vector_2 {
            fill: #AE1F13
          }

          #Vector_3 {
            fill: #E73628;
          }

          rect {
            fill: #E73628;
          }
        }
      }

      .red {
        svg:hover {

          #Vector,
          #Vector_2 {
            fill: #AE1F13
          }

          #Vector_3 {
            fill: #E73628;
          }

          rect {
            fill: #E73628;
          }
        }
      }
    }
  }
}

@media(min-width:1440px) {
  .product-card {
    width: 300px;

    img {
      height: 255px;
      padding: 0 23px;
    }
  }
}

.wishlist {
  width: 26px;
  height: 22px;
  position: fixed;
  bottom: 60px;
  right: 20px;
  z-index: 100;
  background: #F8C144;
  padding: 18px 16px;
  cursor: pointer;
  border-radius: 180px;
  background: #F8C144;
  box-shadow: 0px 0px 0px 0px rgba(204, 98, 0, 0.25) inset, -1px 0px 2px 0px rgba(204, 98, 0, 0.25) inset, -3px -2px 3px 0px rgba(204, 98, 0, 0.21) inset, -6px -4px 4px 0px rgba(204, 98, 0, 0.13) inset, -10px -7px 5px 0px rgba(204, 98, 0, 0.04) inset, -16px -12px 6px 0px rgba(204, 98, 0, 0.00) inset;

  &.red {
    path {
      stroke: none;
      fill: #CB393A;
    }
  }
}

@media(min-width:1024px) {
  .wishlist:hover {
    animation: bounce2 2s ease infinite;

    @keyframes bounce2 {

      0%,
      20%,
      50%,
      80%,
      100% {
        transform: translateY(0);
      }

      40% {
        transform: translateY(-30px);
      }

      60% {
        transform: translateY(-15px);
      }
    }
  }
}



@media(min-width:1440px) {
  .wishlist {
    bottom: 65px;
    right: 100px;
  }
}

@media(min-width:1920px) {
  .wishlist {
    bottom: 65px;
    right: 250px;
  }
}

.up {
  position: fixed;
  bottom: 10px;
  right: 30px;
  z-index: 100;
}

.up:hover {
  rect {
    fill: #B3B3B3;
  }
}

@media(min-width:1440px) {
  .up {
    bottom: 10px;
    right: 110px;
  }
}

@media(min-width:1920px) {
  .up {
    bottom: 10px;
    right: 260px;
  }
}

// cart start

.cart-popup {
  position: absolute;
  top: 60px;
  left: 0;
  display: inline-block;
  transition: inherit;
  text-align: center;
  z-index: 998;
  background: rgba(251, 251, 251, 0.10);
  backdrop-filter: blur(10px);
  width: 100%;
  height: 100%;
  overflow: hidden;
  visibility: visible;
  opacity: 1;

  .cross {
    margin-top: 40px;

    svg {
      cursor: pointer;
      display: block;
      margin-right: 0;
      margin-left: auto;
      padding: 15px;
    }

    .cart-popup_inner {
      position: relative;
      max-width: 320px;
      padding: 16px;
      margin: 0 auto;
      background-color: #2E563C;
      background-image: url(../../public/img/pattern.svg);
      border-radius: 8px;
      background-size: 320%;
      background-position: 150px 0;
      overflow-x: hidden;
      overflow-y: auto;
      box-sizing: content-box;
      z-index: 999;

      ul {
        overflow-y: auto;
        max-height: 460px;

        li {
          outline: none;
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          margin: 0;
          margin-bottom: 8px;
          padding: 16px 8px;
          border-radius: 8px;
          background-color: #FBFBFB;
          box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.25), 0px -2px 8px 0px rgba(0, 0, 0, 0.25);

          img {
            width: 113px;
            height: 113px;
          }

          .item-discription {
            height: 118px;
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            padding: 0;

            .discription-box {
              width: 100%;

              .title {
                color: #1E1E1E;
                font-size: 12px;
                font-weight: 400;
                line-height: 140%;
                letter-spacing: 0.24px;
                text-align: left;
                padding-bottom: 12px;
              }

              .title-weight {
                display: flex;
                justify-content: space-between;

                p {
                  color: #1E1E1E;
                  font-size: 12px;
                  letter-spacing: 0.96px;
                  text-align: left;
                }

                .weight {
                  font-size: 12px;
                  color: #A9A9A9;
                }
              }

              .price {
                font-size: 14px;
                width: 40px;
              }
            }

            .button-box {
              display: flex;
              justify-content: space-between;
              align-items: center;

              .counter {
                height: 22px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 6px 8px;
                border-radius: 5.78px;
                background: #2E563C;

                p {
                  width: 10px;
                  color: #FBFBFB;
                  font-size: 13px;
                  padding: 0 8px;
                }

                .plus {
                  height: 30px;
                  padding-left: 16px;
                  cursor: pointer;

                  svg {
                    padding: 10px 0;
                  }
                }

                .minus {
                  height: 30px;
                  padding-right: 16px;
                  cursor: pointer;
                }

                svg {
                  display: block;
                  margin: auto;
                  padding: 15px 0;
                }
              }

              .delete {
                cursor: pointer;
                display: flex;
                align-items: center;
                width: 20px;
                height: 22px;
                margin-left: 24px;

                svg {
                  height: 16px;
                  padding: 0;
                }
              }
            }
          }
        }
      }

      textarea {
        min-width: 256px;
        max-width: 256px;
        height: 62px;
        margin: 16px 0;
        border-radius: 8px;
        background: #FBFBFB;
        box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.25), 0px -2px 8px 0px rgba(0, 0, 0, 0.25);
        padding: 16px;
        font-family: Manrope;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: normal;
      }

      button {
        padding: 16px 32px;
        border-radius: 8px;
        background: #F8C144;
        border: none;
        font-family: Manrope;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 160%;
        /* 25.6px */
        letter-spacing: 0.32px;

        span {
          font-weight: 700;
          line-height: normal;
          letter-spacing: 1.28px;
          text-transform: uppercase;
        }
      }

      .delivery-information {
        color: #FBFBFB;
        text-align: center;
        font-family: Manrope;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        letter-spacing: 1.44px;
        text-transform: uppercase;
        padding-top: 32px;

        span {
          cursor: pointer;
        }

        a {
          color: #FBFBFB;
          cursor: pointer;
        }
      }

      .telegram {
        display: flex;
        min-height: 68px;
        align-items: flex-end;

        svg {
          padding: 0;
          display: block;
          margin: 0 auto;

          &:hover {
            animation: bounce2 2s ease infinite;

            @keyframes bounce2 {

              0%,
              20%,
              50%,
              80%,
              100% {
                transform: translateY(0);
              }

              40% {
                transform: translateY(-30px);
              }

              60% {
                transform: translateY(-15px);
              }
            }
          }
        }
      }
    }
  }
}

