//cart end

.card-popup {
  position: fixed;
  top: 110px;
  left: 0;
  display: inline-block;
  transition: inherit;
  text-align: center;
  z-index: 998;
  background: rgba(251, 251, 251, 0.10);
  backdrop-filter: blur(2px);
  width: 100%;
  height: 100%;
  overflow: hidden;
  visibility: visible;
  opacity: 1;

  .card-popup_inner {
    position: relative;
    max-width: 320px;
    margin: 0 auto;
    border-radius: 8px 0px 0px 8px;
    background: #FBFBFB;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    overflow-x: hidden;
    overflow-y: auto;
    box-sizing: content-box;
    z-index: 999;

    .image {
      position: relative;

      img {
        height: 320px;
      }

      .cross {
        position: absolute;
        top: 0;
        right: 0;
        padding: 15px;
        cursor: pointer;
      }
    }

    .discription_innder {
      background: #164328;
      padding: 24px 16px;

      .card-discription {
        h2 {
          color: #FBFBFB;
          text-align: left;
          font-size: 20px;
          font-weight: 400;
          letter-spacing: 1.6px;
          padding-bottom: 16px;
        }

        .price-weight {
          display: flex;
          justify-content: space-between;
          padding-bottom: 16px;

          p {
            color: #FBFBFB;
          }
        }

        .discription {
          color: #FBFBFB;
          text-align: left;
          font-weight: 400;
          line-height: 160%;
          /* 25.6px */
          letter-spacing: 0.32px;
          padding-bottom: 24px;
        }
      }

      .buttons {
        .add-to-wishlist {
          cursor: pointer;

          p {
            width: 219px;
            font-size: 16px;
            text-align: center;
            text-transform: uppercase;
            border-radius: 8px;
            background: #F8C144;
            padding: 12px;
            transition: background 9999999s;
          }

          p:hover {
            width: 230px;
            font-size: 18px;
            transition: 0.3s;
          }

          p:active {
            background: #FAD175;
            transition: background 0s;
          }
        }
      }
    }
  }
}

@media(min-width:640px) {
  .card-popup {
    .card-popup_inner {
      max-width: 377px;

      .image {
        img {
          height: 338px;
          flex-shrink: 0;
        }
      }

      .discription_innder {
        padding: 24px;

        .card-discription {
          h2 {
            padding-bottom: 16px;
          }

          .price-weight {
            padding-bottom: 16px;
          }

          .discription {
            padding-bottom: 32px;
          }
        }

        .buttons {
          .add-to-wishlist {
            padding: 16px;
          }
        }
      }
    }
  }
}

